@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@300;400;500&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.App {
  width: 100%;
  font-family: 'Open Sans', sans-serif;
  min-height: 100vh;
}
.App-header {
  display: flex;
  border-bottom: 3px solid #e0354b;
  width: 100%;
  align-items: center;
}
.App-header > img {
  width: 200px;
}

.App-header > h1 {
  color: #e0354b;
  font-weight: bold;
}

.App-body {
  padding: 50px;
  font-weight: bold;
}

.App-body > form {
  font-weight: 300;
  margin-top: 30px;
}

.App-body > form > input {
  height: 30px;
  margin-right: 10px;
}

.App-body > form > input[type='submit'] {
  width: 100px;
  cursor: pointer;
}

.App-footer {
  position: relative;
  bottom: -20px;
  padding: 10px;
  text-align: center;
  width: 100%;
  border-top: 3px solid #e0354b;
  font-weight: bold;
}

.Notifications {
  border: 3px dotted rgb(123, 123, 123);
  padding: 20px;
  color: rgb(123, 123, 123);
  margin: 5px;
}

.Notifications > ul {
  margin-left: 30px;
  margin-top: 10px;
}

.Notifications > ul > li[data-priority='default'] {
  color: blue;
}

.Notifications > ul > li[data-priority='urgent'] {
  color: red;
}

